import React, { useState } from "react";
const Contact = () => {

    const [data , setData] = useState({
        fullname:"",
        phone:"",
        email:"",
        msg:"",
    });

    const InputEvent = (event) => {
   const {name, value } = event.target;   //  obj destructuring 

   // fetching the data that the user has filled above and then setting it with the help of setData(preval), were the preval is Previous Value
   setData((preVal) => {
       return {
           ...preVal,
           [name]: value,
       };
   });
    };

const formSubmit = (e) => {
  e.preventDefault(); // avoid the page refresh
  alert(`your name is ${data.fullname}. Your Mobile Number is ${data.phone}. your Email is ${data.email}. you suggested ${data.msg}`)
};



    return (
        <>
            <div className="my-5">
                <h1 className="text-center"> Contact Us </h1>
            </div>
            <div className="container contact_div">
                <div className="row">
                    <div className="col-md-6 col-10 mx-auto">

                        <form onSubmit={formSubmit}>

                            <div className="mb-3">
                                <label for="exampleFormControlInput1" className="form-label">Enter Your Full Name </label>
                                <input type="text" className="form-control" id="exampleFormControlInput1" placeholder="Enter your name" 
                                    name="fullname"
                                    value={data.fullname}
                                    onChange={InputEvent}
                                />
                            </div>

                            <div className="mb-3">
                                <label for="exampleFormControlInput1" className="form-label">Phone</label>
                                <input type="number" className="form-control" id="exampleFormControlInput1" placeholder="Mobile number" 
                                    name="phone"
                                    value={data.phone}
                                    onChange={InputEvent}
                                />
                            </div>

                            <div className="mb-3">
                                <label for="exampleFormControlInput1" className="form-label">Email address</label>
                                <input type="email" className="form-control" id="exampleFormControlInput1" placeholder="name@example.com" 
                                    name="email"
                                    value={data.email}
                                    onChange={InputEvent}
                                />
                            </div>


                            <div className="mb-3">
                                <label for="exampleFormControlTextarea1" className="form-label"> Message </label>
                                <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"
                                name="msg"
                                    value={data.msg}
                                    onChange={InputEvent}
                                    >
                                </textarea>
                            </div>

                            <div class="col-12">
                                <button class="btn btn-outline-primary" type="submit">Submit Now</button>
                            </div>

                        </form>

                    </div>
                </div>
            </div>

        </>
    );
};
export default Contact;


// col-md-6 ------> normal Dom me we will give only 6 column
// col-10  -------> in mobile view 10 column
// mx-auto ------> taki humesha center me dekhaaii de
